import {
    CLEAR_JOIN_ERROR,
    CLEAR_JOIN_MESSAGE,
    JOIN_FAIL,
    JOIN_LOADING_START,
    JOIN_SUCCESS,
} from "../types";
import {fetchRequest, joinUrl, setError} from "./fetchTools";


export const join = (data,onResetForm) => async (dispatch) => {
    dispatch(clearJoinError())
    dispatch({type: JOIN_LOADING_START})
    try {
        const formData = new FormData();

        for (const key in data) {
            formData.append(key, data[key]);
        }
        // eslint-disable-next-line no-unused-vars
        const fetchData = await fetchRequest(joinUrl, 'POST', formData,{'Content-Type': 'multipart/form-data'})
        dispatch({type: JOIN_SUCCESS})
        onResetForm()

        setTimeout(() => dispatch(clearJoinMessage()),3000)
    } catch (err) {
        dispatch(setJoinError(err.message))
    }
}

export const setJoinError = (payload) => dispatch => {
    dispatch(setError(payload,JOIN_FAIL))
}

export const clearJoinError = () => dispatch => {
    dispatch({type: CLEAR_JOIN_ERROR})
}

export const clearJoinMessage = () => dispatch => {
    dispatch({type: CLEAR_JOIN_MESSAGE})
}

