import React, {useEffect} from 'react';
import {useParams} from "react-router-dom";
import {useFormValue} from "../../../hooks/useFormValue";
import {useDispatch, useSelector} from "react-redux";
import {clearJoinError, clearJoinMessage, join, setJoinError} from "../../../redux/action/join";

import FormDef from "../../layout/FormDef/FormDef";
import InputDef from "../../layout/InputDef/InputDef";
import Svg from "../../layout/Svg/Svg";

import styles from "./JoinForm.module.scss"
import {attachFileIcon} from "../../../assets/svg";
import {clearSelectedProfession, getPositionList} from "../../../redux/action/career";

function JoinForm() {
    const dispatch = useDispatch()
    const {professionId} = useParams()
    const {error, success, loading} = useSelector(state => state.join)
    const {selectedProfession} = useSelector(state => state.career)
    const {
        formData,
        onChange,
        onResetForm,
        onPhoneChange,
        clearInputError,
        setFormData,
    } = useFormValue({
        first_name: '',
        last_name: '',
        email: '',
        phone: '',
        document: null,
        message: '',
        position: selectedProfession ? selectedProfession?.title : '',
    }, setJoinError, error)

    useEffect(() => {
        return () => {
            error && dispatch(clearJoinError())
            success && dispatch(clearJoinMessage())
            dispatch(clearSelectedProfession())
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    useEffect(() => {
        dispatch(getPositionList(professionId))
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    useEffect(() => {
        setFormData(prevState => ({
            ...prevState,
            position: selectedProfession?.title
        }))
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [selectedProfession])

    const onUploadFile = e => {
        clearInputError('document')
        setFormData(prevState => ({
            ...prevState,
            document: e.target.files[0]
        }))
    }

    const onSubmit = (e) => {
        e.preventDefault()
        dispatch(join(formData, onResetForm))
    }

    const errorProp = error && typeof error !== 'object' ? error : null

    return (
        <FormDef
            className={styles['joinForm']}
            onSubmit={onSubmit}
            isMultipart={true}
            errorProp={errorProp}
            successProp={success}
            successText={'Your CV was sent successfully.'}
            loading={loading}
        >
            <InputDef
                name='first_name'
                value={formData.first_name}
                errorProp={error?.first_name}
                onChange={onChange}
                type="text"
                placeholder={<>First Name<sup>*</sup></>}
            />
            <InputDef
                name='last_name'
                value={formData.last_name}
                errorProp={error?.last_name}
                onChange={onChange}
                type="text"
                placeholder={<>Last Name<sup>*</sup></>}
            />
            <InputDef
                name='email'
                value={formData.email}
                errorProp={error?.email}
                onChange={onChange}
                placeholder={<>Email<sup>*</sup></>}
            />
            <InputDef
                name='phone'
                value={formData.phone}
                errorProp={error?.phone}
                onChange={onPhoneChange}
                type="tel"
                placeholder={<>Phone<sup>*</sup></>}
            />
            <InputDef
                errorProp={error?.document}
            >
                <label htmlFor="joinFormFileInput" className={`${styles['joinForm__fileLabel']} inputDef`}>
                <input
                    type="file"
                    id='joinFormFileInput'
                    className={styles['joinForm__fileInput']}
                    onChange={onUploadFile}
                    name='document'
                    accept='application/vnd.openxmlformats-officedocument.wordprocessingml.document,application/msword,application/pdf'
                />
                    <Svg id={attachFileIcon} className={styles['joinForm__fileAttachIcon']}/>
                    <span>{formData.document ? formData.document?.name : 'Attach your CV'}</span>
                </label>
            </InputDef>
            <p className={`inputDef inputDef_disabled ${styles['joinForm__profession']} ${selectedProfession?.title ? styles['joinForm__profession_active'] : ''}`}>{selectedProfession?.title || 'Profession'}</p>
            <InputDef
                name='message'
                value={formData.message}
                errorProp={error?.message}
                onChange={onChange}
                isTextArea={true}
                placeholder='Motivation Letter'
            />
        </FormDef>
    );
}

export default JoinForm;